@import url(https://fonts.googleapis.com/css?family=Raleway);
@import url(https://fonts.googleapis.com/css?family=Roboto+Mono);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*{
  margin: 0;
  padding: 0;
}

.website-one {
  font-family: 'Raleway', sans-serif;
}

.landing-page-one {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: url(/static/media/bg.4160eca8.jpg);
  background-size: cover;
  background-attachment: fixed;

}

.intro-box {
  width: calc(320px + 8vw);
  height: calc(320px + 8vw);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-color: #f4f4f4;
}

.intro-box img {
  width: calc(140px + 4vw);
  border-radius: 50%;
}

.title {
  margin: 5px;
  font-size: calc(30px + .5vw);
}

.position {
  font-size: calc(15px + .5vw);
}

.links {
  margin-top: 25px;
}

.links ul {
  display: flex;
  width: 25vw;
  justify-content: center;
  list-style-type: none;
}

.links ul li {
  margin: 0 10px 0 10px;
}

a {
  color: inherit;
}

.page {
  margin: 100px 10vw;
}

.intro {
  font-size: calc(3vw + 30px);
  color: #777777;
  margin-top: 80px;
  margin-bottom: 25px;

}

.content {
  color: #acb5c4;
  font-size: calc(.6vw + 15px);
  text-align: justify;
}

.portfolio {
  display: flex;
  flex-direction: column;
}

.portfolio-title {
  font-size: calc(3vw + 30px);
  color: #777777;
  text-align: left;
}

.portfolio-grid {
  margin-top: 30px;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(20rem, 1fr));
  grid-template-rows: auto;
  grid-gap: 10px 10px;
  justify-items:center;
}

.project a img {
  min-height: 200px;
  min-width: 300px;
}


.skills-grid {
  display: grid;
  margin-top: 30px;
  grid-template-columns: repeat(auto-fill, minmax(12rem, 1fr));
  grid-gap: 50px 6px;
  grid-template-rows: auto;

}

.skill {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.skill svg {
  width: 80%;
}

.skills-title {
  font-size: calc(3vw + 30px);
  color: #777777;
}

.page {
  margin: 100px 10vw;
}

.contact-box {
  width: 100%;
}

.contact-title-one {
  font-size: calc(3vw + 30px);
  color: #777777;
}

.change-style {
  margin-top: 3vw;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: #777777;
}

.change-style {
  font-size: calc(1vw + 10px);
}

.footerOne {
  display: flex;
  align-items: center;
  justify-content: center;
  height: calc(1vw + 30px);
  color: white;
  background-color: #777777;
}

@media only screen and (max-width: 600px) {
  .skills-grid {
  grid-template-columns: repeat(auto-fill, minmax(10rem, 1fr));
  }

}
* {
	margin: 0;
	padding: 0;
	
}

.website-two {
	color: #A3A3A3;
	font-family: 'Roboto Mono', monospace;
}

.fixed {
	background-image: url(/static/media/bg-2.eae31dd2.jpg);
	height: 100vh;
	background-size: cover;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	position: fixed;
	width: calc(20vw + 150px);
}

.scroll {
	margin-left: calc(20vw + 150px);
}

.profile-two {
	width: calc(250px + 5vw);
	height: calc(12vw + 200px);
	color:white;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: flex-end;
}

.profile-two img {
	width: calc(140px + 2vw);
	border-radius: 50%;
	margin-bottom: 30px;
}

.profile-two-title {
	font-size: calc(1vw + 15px);
}

.profile-two-content {
	font-size: calc(.7vw + 10px);
	margin-bottom: 30px;
}

.links-two ul {
	display: flex;
}

.links-two ul li {
	margin-left: 15px;
}

.links-two ul li:hover {
	color: #D26D3E;
}


.intro-two {
	height: 100vh;
	display: flex;
	justify-content: center;
	align-items: center;
	padding: 0px 5vw;
}

.intro-two-box {
	height: calc(12vw + 200px);
	display: flex;
	justify-content: space-between;
	align-items: center;
	flex-direction: column;

}

.intro-two-title {
	font-size: calc(1.8vw + 20px);
	color: #777777;
}

.content-two {
	font-size: calc(.5vw + 12px);
	text-align: justify;
}

.intro-two button {
	align-self: flex-start;
}

.page-two {
	padding: 40px 5vw;
}

.portfolio {
  display: flex;
  flex-direction: column;
}

.title-two {
  font-size: calc(3vw + 30px);
  color: #777777;
  text-align: left;
}

.portfolio-grid-two {
  margin-top: 50px;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(20rem, 1fr));
  grid-template-rows: auto;
  grid-gap: 30px 50px;
  justify-items:center;
}

.skills-grid-two {
  display: grid;
  margin-top: 30px;
  grid-template-columns: repeat(auto-fill, minmax(10rem, 1fr));
  grid-gap: 50px 6px;
  grid-template-rows: auto;

}

.skill {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.skill svg {
  width: 80%;
}



.contact-title-two {
  font-size: calc(3vw + 30px);
  color: #777777;
}

.change-style {
  font-size: calc(1vw + 10px);
}

.change-style button {
  margin-top: 15px;
}

.skills-title-two {
  font-size: calc(3vw + 30px);
}

.footer-two {
  margin-left: calc(20vw + 150px);
  display: flex;
  align-items: center;
  justify-content: center;
  height: calc(1vw + 30px);
  color: white;
  background-color: #777777;
}

@media only screen and (max-width: 800px) {
	.fixed {
		height: 100vh;
		position: relative;
		width: 100vw;

	}

	.profile-two {
		height: auto;
	}

	.scroll {
		margin: 0 30px;
	}

	.profile-two {
		align-items: center;
	}

	.intro-two {
		height: auto;
	}

	.intro-two-box {
		margin: 40px 0;
		height: 450px;
	}

	.footer-two {
  		margin-left: 0;
	}
}
